import React, {Fragment, useEffect} from "react";
import {Link, Navigate} from "react-router-dom";
import {useForm} from "react-hook-form";

import {useDispatch, useSelector} from "react-redux";
import { selectloggedInUser } from "../authSlice";
import { SignInUserAsync, SignOutUserAsync } from '../authSlice';


import { useAlert } from "react-alert";



export default function SignIn() {
  const alert = useAlert();
  const dispatch = useDispatch();
  const loggedInUser = useSelector(selectloggedInUser);


  const { register, handleSubmit, formState: {errors} } = useForm();

  useEffect(()=>{
    dispatch(SignOutUserAsync())
  },[dispatch])

  const onSubmit = (data) => dispatch(SignInUserAsync(data)); 
  
  if(loggedInUser){
    const { success, message } = loggedInUser;
    (success) ? alert.success(message) : alert.error("User Not Found ")
  }

  return (
    <Fragment>
      {loggedInUser?.success === false && <Navigate to='/signup' replace={true}></Navigate>}
      {loggedInUser?.success === true && <Navigate to='/' replace={true}></Navigate>}
      <div className='flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8'>
        <div className='sm:mx-auto sm:w-full sm:max-w-sm'>
          <img
            alt='Your Company'
            src='data:image/jpeg;base64,'
            className='mx-auto h-50 w-auto'
          />
          <h2 className='mt-3 text-center text-2xl font-bold leading-9 tracking-tight text-gray-900'>
            Sign in to your account
          </h2>
        </div>

        <div className='mt-10 sm:mx-auto sm:w-full sm:max-w-sm'>
          {/* singin form */}
          <form  onSubmit={handleSubmit(onSubmit)} className='space-y-6'>
            <div>
              <label
                htmlFor='email'
                className='block text-sm font-medium leading-6 text-gray-900'
              >
                Email address
              </label>
              <div className='mt-2'>
                <input
                  type='text'
                  {...register("email", {
                    required: "Please Enter Your Email!",
                    pattern: {
                        value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i, 
                        message: "Please Enter A Valid Email!"
                    }
                })}
                  className='block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6'
                />
              </div>
              {errors.email && <span className="text-red-500">{errors.email.message}</span>}
            </div>

            <div>
              <div className='flex items-center justify-between'>
                <label
                  htmlFor='password'
                  className='block text-sm font-medium leading-6 text-gray-900'
                >
                  Password
                </label>
                <div className='text-sm'>
                  <Link
                    to='/forgot_password'
                    className='font-semibold text-indigo-600 hover:text-indigo-500'
                  >
                    Forgot password?
                  </Link>
                </div>
              </div>
              <div className='mt-2'>
                <input
                  type='password'
                  {...register("password",{ required: true }) }
                  className='block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6'
                />
              </div>
              {errors.password && <span className="text-red-500">This field is required</span>}
            </div>

            <div>
              <button
                type='submit'
                className='flex w-full justify-center rounded-md bg-indigo-600 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600'
              >
                Sign in
              </button>
            </div>
          </form>

          <p className='mt-10 text-center text-sm text-gray-500'>
            Not a member?{" "}
            <Link
              to='/signup'
              className='font-semibold leading-6 text-indigo-600 hover:text-indigo-500'
            >
              create an account
            </Link>
          </p>
        </div>
      </div>
    </Fragment>
  );
}
